.why {

  &__box {
    padding: 25px 140px;
    position: relative;

    &-img {
      position: absolute;
      left: -200%;
      top: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;
      opacity: 0;
      transition: 1.2s;
    }

    &-title {
      font-style: italic;
      font-weight: 800;
      font-size: 12vh;
      line-height: 1.2;
      margin-left: 42px;
      // transition: 1.8s;
      transition: left 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 0s;
      position: relative;
      left: -150%;
    }

    &-subtitle {
      font-weight: 600;
      font-size: 5vh;
      line-height: 1.2;
      margin-top: 10px;
      margin-left: 32px;
      // transition: 1.8s;
      transition: left 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 0s;
      position: relative;
      left: -200%;
    }

    &-text {
      font-family: $accent-font;
      font-size: 3vh;
      margin-top: 15px;
      // transition: 1.8s;
      transition: bottom 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 0s;
      position: relative;
      bottom: -200%;
    }
  }

  &__title {
    font-style: italic;
    font-weight: bold;
    font-size: 4vh;
    margin: 23px 0 36px;
    transition: 1.8s;
    transition-delay: 1.6s;
    height: 0;
    opacity: 0;
  }

  &__items {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    transition: 1.8s;
    transition-delay: 1.6s;
    height: 0;
    opacity: 0;

  }

  &__item {
    font-style: normal;
    font-weight: 600;
    font-size: 2vh;
    text-transform: uppercase;
    color: $prime-color;
    margin: 0 5px;
  }
}
.swiper-slide-active .why__box-img {
  left: 50%;
  opacity: 1;
}

.swiper-slide-active .why__box-title {
  left: 0;
}

.swiper-slide-active .why__box-subtitle {
  left: 0;
}

.swiper-slide-active .why__box-text {
  bottom: 0;
}

.swiper-slide-active .why__title,
.swiper-slide-active .why__items {
  opacity: 1;
  height: auto;
  // transition: 1.8s;
}

.why-issues .why__box {
  padding: 10% 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.why-issues .why__box-title {
  font-size: 40px;
  font-weight: 600;
  margin-left: 0;
  margin-bottom: 25px;
  flex: 1 1 auto;
}

.why-issues__items {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  height: 35vh;
  max-width: 100%;
  align-items: flex-start;
  max-width: 58%;
}

.why-issues__item {
  padding: 23px 25px 24px 90px;
  position: relative;
  border-left: 1px solid #fff;
  font-size: 12px;
  width: 660px;
  max-width: 100%;
  position: relative;
  color: #000;
  transform: translateX(-150vw);

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: left 10px center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -2;
  }

  &:nth-of-type(1)::before {
    background-image: url('../img/bez-1.png');
  }

  &:nth-of-type(2)::before {
    background-image: url('../img/bez-2.png');
  }

  &:nth-of-type(3)::before {
    background-image: url('../img/bez-3.png');
  }

  &:nth-of-type(4)::before {
    background-image: url('../img/bez-4.png');
  }

  &::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    transform: scaleX(0);
    transform-origin: left center 0;
    top: 0;
    left: 0;
    z-index: -1;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: left center;
  }

  &:nth-of-type(1)::after {
    transition: background-image 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 1s;
    background-image: url('../img/line-white.svg');
  }

  &:nth-of-type(2)::after {
    transition: background-image 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 1.5s;
    background-image: url('../img/line-orange.svg');
  }

  &:nth-of-type(3)::after {
    transition: background-image 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 2s;
    background-image: url('../img/line-white-t.svg');
  }

  &:nth-of-type(4)::after {
    transition: background-image 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 2.5s;
    background-image: url('../img/line-orange-t.svg');
  }
}

.why-issues__text {
  width: 39%;
  font-family: $accent-font;
  font-size: 26px;
  transform: translateY(100vh);
}

.swiper-slide-active .why-issues__item {
  transform: translateY(0);
  transition: transform 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 0s;

  &:nth-of-type(1)::after {
    transition: transform 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 1s;
    transform: scale(1.0);
  }

  &:nth-of-type(2)::after {
    transition: transform 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 1.5s;
    transform: scale(1.0);
  }

  &:nth-of-type(3)::after {
    transition: transform 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 2s;
    transform: scale(1.0);
  }

  &:nth-of-type(4)::after {
    transition: transform 1.8s cubic-bezier(0.1, 0.39, 0, 1.12) 2.5s;
    transform: scale(1.0);
  }
}

.swiper-slide-active .why-issues__text {
  transform: translateY(0);
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 3.5s;
}

.why-form__title {
  margin-left: 0;
  margin-bottom: 0;
  font-size: 73px;
  line-height: 1.2;
  text-transform: uppercase;
  transform: translateY(50px);
  opacity: 0;
  transition: 1s;
}

.why-form__text {
  font-size: 19px;
  color: #7B7B7B;
  margin-bottom: 26px;
  opacity: 0;
  transition: 1s;
}

.swiper-slide-active .why-form__title,
.project__order .why-form__title {
  transform: translateY(0);
  opacity: 1;
}

.swiper-slide-active .why-form__text,
.project__order .why-form__text {
  opacity: 1;
}
