body {
  min-height: 100vh;
  min-width: 375px;
  line-height: 1.3;
  font-family: $regular-font;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

a {
  color: inherit;
  display: inline-block;
  transition: 0.5s;

  &:hover,
  &:focus {
    color: $prime-color;
  }
}

ul[class],
ol[class] {
  padding: 0;
}


html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 800;
  font-style: italic;
  line-height: 1.2;
}

ul[class],
ol[class] {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: inline-block;
}

button {
  border: none;
  padding: 0;
  background-color: transparent;
  box-shadow: none;
  cursor: pointer;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

@media (min-width: 786px) {
  body {
    font-size: 17px;
  }

  .full-screen {
    overflow: hidden;
  }
}
