.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  padding: 10px 0;
  width: 100%;
}

.header__logo-img {
  transform-origin: top;
  transition: transform 0.8s ease-in-out;
}


.pages__header {
  position: static;

  & .wrapper {
    position: relative;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__phone,
.header__contact,
.header__menu {
  font-style: italic;
  font-weight: 800;
  font-size: 24px;
  text-align: right;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  margin-left: 20px;
  transition: 0.3s;
}

.header__menu {
  display: flex;
}

.header.open {
  position: fixed;

  & .back {
    opacity: 0;
    transform: translateX(100vw);
  }
}

.header.open,
.pages__header {
  & .header__phone,
  & .header__contact,
  & .header__menu {
    color: #000;

    &:hover,
    &:focus {
      color: $prime-color;
    }
  }
}

.header__right {
  display: flex;
}

.header__menu {
  cursor: pointer;
  display: flex;
  transition: 0.8s;

  &:hover {
    color: $prime-color;
  }
}

.header__menu span {
  -webkit-text-stroke: 1px $prime-color;
  -webkit-text-fill-color: transparent;
  text-stroke: 1px $prime-color;
  text-fill-color: transparent;
}

.header__menu.open {
  font-size: 0;

  & > span::before {
    content: "д";
    font-size: 24px;
    font-family: inherit;
    font-style: italic;
    font-weight: inherit;
    -webkit-text-stroke: 1px $prime-color;
    -webkit-text-fill-color: transparent;
    text-stroke: 1px $prime-color;
    text-fill-color: transparent;
  }

  &::before {
    content: "Наза";
    font-size: 24px;
    font-family: inherit;
    font-style: italic;
    font-weight: inherit;
    -webkit-text-stroke: 1px #000;
    -webkit-text-fill-color: transparent;
    text-stroke: 1px #000;
    text-fill-color: transparent;
  }
}

.main__nav {
  background: #fff;
  color: #000;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  opacity: 0;
  transform: translateX(150vw);
  transition: 0.8s;
  z-index: -1;

  &.open {
    opacity: 1;
    transform: translateX(0);
  }

  & .main-nav__menu {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 150px auto 0;
    background: url('../img/menu-bg.png') no-repeat right center/auto 100%;
  }
}


.main-nav__item {
  text-transform: uppercase;
  font-size: 10vh;
  font-weight: 900;
  font-style: italic;
  transition: 0.5s;

  &:nth-of-type(even) {
    font-size: 8vh;
  }

  & a {
    color: #000;
    text-decoration: none;
    transition: 0.6s;

    &::before,
    &::before,
    &::before {
      content: "";
      width: 0;
      height: 10vh;
      display: inline-block;
      vertical-align: middle;
      background: url('../img/menu-dot.png') no-repeat left center/contain;
      transition: 0.7s;
    }
  }

  &.active::before {
    content: "";
    width: 0;
    height: 10vh;
    display: inline-block;
    vertical-align: middle;
    background: url('../img/menu-dot.png') no-repeat left center/contain;
    transition: 0.7s;
  }

  &:hover a,
  &:focus a,
  &:active a,
  &.active {
    -webkit-text-stroke: 1px $prime-color;
    -webkit-text-fill-color: transparent;
    text-stroke: 1px $prime-color;
    text-fill-color: transparent;


    &::before,
    &::before,
    &::before {
      width: 102px;
    }
  }
}

.back {
  text-stroke: 1px $prime-color;
  text-fill-color: transparent;
  -webkit-text-stroke: 1px $prime-color;
  -webkit-text-fill-color: transparent;
  position: absolute;
  right: 20px;
  top: 90px;
  font-style: italic;
  font-weight: 900;
  font-size: 51px;
  text-transform: uppercase;
  text-decoration: none;

  &:hover,
  &:focus,
  &:active {
    color: $prime-color;
    text-fill-color: $prime-color;
    -webkit-text-fill-color: $prime-color;
    transition: 0.8s;
  }
}

@media (max-width: $mobile-width) {
  .header__phone {
    font-size: 0;
    width: 26px;
    height: 26px;
    background: url('../img/phone.svg') no-repeat center/contain;
  }

  .header__menu {
    font-size: 0;
  }

  .header__burger {
    width: 30px;
    height: 18px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transform: skew(10deg);

    & span {
      height: 2px;
      width: 100%;
      background: #fff;
      transition: 0.8s ease-out;
    }
  }

  .header__menu.open span::before,
  .header__menu.open::before {
    font-size: 0;
  }

  .header__menu.open .header__burger {
    transform: none;

    & span {
      background: $prime-color;
    }

    & span:nth-of-type(1) {
      transform: rotate(230deg) translateX(-10px);
    }

    & span:nth-of-type(2) {
      transform: scale(0);
    }

    & span:nth-of-type(3) {
      transform: rotate(-230deg) translateX(-10px);
    }
  }

  .header__right {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
  }

  .header__logo-img {
    width: 115px;
  }

  .header__contact {
    position: absolute;
    bottom: -50px;
    right: 20px;
    font-size: 22px;

    &::after {
      content: "";
      width: calc(100% + 50px);
      height: 2px;
      position: absolute;
      background: $prime-color;
      bottom: 0;
      left: 0;
    }
  }
}
