.about {
  position: relative;
  width: 100%;
  height: 100%;
}
.about_bg {
  position: absolute;
  right: 10%;
  top: 50%;
  transform: translateY(-60%);
}
.about__content {
  max-width: 55%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  transform: translateY(100%);
  transition: 1.5s;
}
.swiper-slide-active .about__content {
    transform: translateY(0);
}
.about__content-title {
  font-size: 100px;
  color: $prime-color;
}
.about__content-text {
  font-family: $accent-font;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  letter-spacing: -0.01em;
  margin-top: 40px;
  margin-left: 35px;
}
.about__span-text {
  font-family: $regular-font;
  font-style: italic;
  font-weight: 800;
  font-size: 30px;
  line-height: 37px;
}
.about__span-text2 {
  font-family: $regular-font;
  font-style: italic;
  font-weight: 800;
  font-size: 30px;
  -webkit-text-stroke: 1px $prime-color;
  -webkit-text-fill-color: transparent;
}

.about2__img {
    object-position: left;
    position: fixed;
    right: 0;
    z-index: -1;
    left: 200%;
    transition: 2.5s;
    width: 100%;
    margin-left: -48%;

}
.swiper-slide-active .about2__img {
    left: 0;
    -webkit-transition: left 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0s;
    transition: left 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0s;
}
.about2__left-text {
font-family: $accent-font;
font-style: normal;
font-weight: normal;
font-size: 24px;
color: #000000;
margin-top: 110px;
margin-left: 95px;
}
.about2 {
    display: flex;
    align-items: center;
}
.about2__left {
    width: 50%;
    background-image: url('../img/Rectangle63.svg');
    background-position: bottom left;
    background-repeat: no-repeat;
    background-size: contain;
    padding-bottom: 35px;
    margin-left: -30px;
    padding-left: 12px;

}
.about2__right {
    width: 50%;
}
.about2_left-left,
.about2_left-right {
font-style: normal;
font-weight: bold;
font-size: 20px;
width: 50%;
color: #000000;
& li {
    padding: 10px 0;
}
}
.about2__left-box {
    display: flex;
    align-items: center;
    width: 97%;
    margin-left: auto;
    margin-top: 15px;
}
.swiper-slide-active {
    z-index: 4;
    position: relative;
}
.about2_right {
    color: #fff;
    width: 50%;
    background-image: url('../img/Rectangle64.svg');
    background-position: top right;
    background-repeat: no-repeat;
    background-size: contain;
    padding-top: 60px;
    &-text {
        font-family: $accent-font;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        margin-left: 100px;
    }
    &-box {
        display: flex;
        margin-top: 15px;
        width: 110%;
    }
    &-left,
    &-right {
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        width: 50%;
        & li {
            padding: 10px 0;
        }
    }
}
.about3 {
  transition: 1.8s;
  bottom: -200%;
  position: relative;
  &_box {
    position: relative;
  }
  &__title {
    font-size: 100px;
    color: #000000;
    background-color: #FFFFFF;
    display: inline-block;
    padding: 0 15px;
    position: relative;
  }
  &__p-top {
    font-family: $accent-font;
    font-style: normal;
    font-weight: normal;
    font-size: 23px;
    margin-top: 40px;
  }

  &__p-bottom {
    font-family: $accent-font;
    font-style: normal;
    font-weight: normal;
    font-size: 23px;
    margin-top: 30px;
    width: 50%;

    & span{
      font-style: italic;
      font-weight: 900;
      font-size: 33px;
    }
  }
  &__form {
    font-size: 27px;
    padding: 33px 60px;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 30px;
  }
}
.swiper-slide-active .about3 {
  transition: bottom 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0s;
  bottom: 0;
}
.about3__title::before {
  content: '';
  position: absolute;
  width: 200%;
  background-color: #fff;
  height: 122px;
  z-index: -1;
  transition: 1.2s;
  transition-delay: 1.1s;
  transform: translateX(-50%);
  left: 50%;
}
.swiper-slide-active .about3__title::before {
  width: 0;
}
