@charset "UTF-8";
/*Шрифты*/
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;0,800;0,900;1,400;1,600;1,700;1,900&display=swap");
@font-face {
  font-family: "Equinox";
  src: url("../fonts/Equinox-Regular.woff") format("woff"), url("../fonts/Equinox-Regular.woff2") format("woff2");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}
/*Переменные*/
.wrapper {
  max-width: 1420px;
  padding: 0 20px;
  margin: 0 auto;
}

.hidden {
  display: none;
  opacity: 0;
  position: absolute;
  transform: translate(-500%);
  width: 0;
  height: 0;
  visibility: hidden;
}

body {
  min-height: 100vh;
  min-width: 375px;
  line-height: 1.3;
  font-family: "Montserrat", sans-serif;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

a {
  color: inherit;
  display: inline-block;
  transition: 0.5s;
}
a:hover, a:focus {
  color: #e35d14;
}

ul[class],
ol[class] {
  padding: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 800;
  font-style: italic;
  line-height: 1.2;
}

ul[class],
ol[class] {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: inline-block;
}

button {
  border: none;
  padding: 0;
  background-color: transparent;
  box-shadow: none;
  cursor: pointer;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
@media (min-width: 786px) {
  body {
    font-size: 17px;
  }

  .full-screen {
    overflow: hidden;
  }
}
