.button {
  font-weight: 700;
  font-size: 27px;
  text-transform: uppercase;
  padding: 31px 52px 33px;
  display: inline-block;
  position: relative;
  margin-top: 30px;
  color: #fff;
  text-decoration: none;
  font-style: italic;
  transition: 0.5s;
  cursor: pointer;
}
.button::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  border: 1px solid #e35d14;
  transform: skewX(-10deg);
  z-index: -1;
  transition: 0.5s;
}
.button:hover, .button:focus, .button:active {
  color: #fff;
}
.button:hover::after, .button:focus::after, .button:active::after {
  background-color: #e35d14;
}

.button--white::after {
  border-color: #fff;
}
.button--white:hover, .button--white:focus, .button--white:active {
  color: #000;
}
.button--white:hover::after, .button--white:focus::after, .button--white:active::after {
  background-color: #fff;
}

.button--black {
  color: #fff;
}
.button--black::after {
  background-color: #000;
  border-color: transparent;
}
.button--black:hover, .button--black:focus, .button--black:active {
  color: #000;
}
.button--black:hover::after, .button--black:focus::after, .button--black:active::after {
  border-color: #000;
  background-color: transparent;
}

.page {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  background-color: #000000;
  color: #fff;
}

.page__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.screen {
  flex: 1 0 100%;
  position: relative;
}

.screen__content {
  padding: 150px 25px 25px;
  height: 100%;
  opacity: 0;
  transition: 1.5s;
}

.swiper-slide-active .screen__content {
  opacity: 1;
  transition: 1.5s;
}

.strelca,
.strelca-top {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  stroke: #707070;
  transition: 0.8s;
}
.strelca path,
.strelca-top path {
  fill: #707070;
}

.strelca-top {
  top: 20px;
  bottom: auto;
  transform: rotate(180deg);
  z-index: 11;
}

.strelca:hover,
.top:hover,
.strelca-top:hover {
  stroke: #e35d14;
}
.strelca:hover path,
.top:hover path,
.strelca-top:hover path {
  fill: #e35d14;
}

.strelca.swiper-button-disabled,
.top.swiper-button-disabled {
  transform: translateY(150vh);
}

.strelca-top.swiper-button-disabled {
  transform: translateY(-150vh);
}

.top {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateY(150vh);
  cursor: pointer;
  stroke: #707070;
  transition: 0.8s;
}
.top path {
  fill: #707070;
}
.top.active {
  transform: translateX(-50%) rotate(180deg);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  padding: 10px 0;
  width: 100%;
}

.header__logo-img {
  transform-origin: top;
  transition: transform 0.8s ease-in-out;
}

.pages__header {
  position: static;
}
.pages__header .wrapper {
  position: relative;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__phone,
.header__contact,
.header__menu {
  font-style: italic;
  font-weight: 800;
  font-size: 24px;
  text-align: right;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  margin-left: 20px;
  transition: 0.3s;
}

.header__menu {
  display: flex;
}

.header.open {
  position: fixed;
}
.header.open .back {
  opacity: 0;
  transform: translateX(100vw);
}

.header.open .header__phone, .header.open .header__contact, .header.open .header__menu,
.pages__header .header__phone,
.pages__header .header__contact,
.pages__header .header__menu {
  color: #000;
}
.header.open .header__phone:hover, .header.open .header__phone:focus, .header.open .header__contact:hover, .header.open .header__contact:focus, .header.open .header__menu:hover, .header.open .header__menu:focus,
.pages__header .header__phone:hover,
.pages__header .header__phone:focus,
.pages__header .header__contact:hover,
.pages__header .header__contact:focus,
.pages__header .header__menu:hover,
.pages__header .header__menu:focus {
  color: #e35d14;
}

.header__right {
  display: flex;
}

.header__menu {
  cursor: pointer;
  display: flex;
  transition: 0.8s;
}
.header__menu:hover {
  color: #e35d14;
}

.header__menu span {
  -webkit-text-stroke: 1px #e35d14;
  -webkit-text-fill-color: transparent;
  text-stroke: 1px #e35d14;
  text-fill-color: transparent;
}

.header__menu.open {
  font-size: 0;
}
.header__menu.open > span::before {
  content: "д";
  font-size: 24px;
  font-family: inherit;
  font-style: italic;
  font-weight: inherit;
  -webkit-text-stroke: 1px #e35d14;
  -webkit-text-fill-color: transparent;
  text-stroke: 1px #e35d14;
  text-fill-color: transparent;
}
.header__menu.open::before {
  content: "Наза";
  font-size: 24px;
  font-family: inherit;
  font-style: italic;
  font-weight: inherit;
  -webkit-text-stroke: 1px #000;
  -webkit-text-fill-color: transparent;
  text-stroke: 1px #000;
  text-fill-color: transparent;
}

.main__nav {
  background: #fff;
  color: #000;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  opacity: 0;
  transform: translateX(150vw);
  transition: 0.8s;
  z-index: -1;
}
.main__nav.open {
  opacity: 1;
  transform: translateX(0);
}
.main__nav .main-nav__menu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 150px auto 0;
  background: url("../img/menu-bg.png") no-repeat right center/auto 100%;
}

.main-nav__item {
  text-transform: uppercase;
  font-size: 10vh;
  font-weight: 900;
  font-style: italic;
  transition: 0.5s;
}
.main-nav__item:nth-of-type(even) {
  font-size: 8vh;
}
.main-nav__item a {
  color: #000;
  text-decoration: none;
  transition: 0.6s;
}
.main-nav__item a::before, .main-nav__item a::before, .main-nav__item a::before {
  content: "";
  width: 0;
  height: 10vh;
  display: inline-block;
  vertical-align: middle;
  background: url("../img/menu-dot.png") no-repeat left center/contain;
  transition: 0.7s;
}
.main-nav__item.active::before {
  content: "";
  width: 0;
  height: 10vh;
  display: inline-block;
  vertical-align: middle;
  background: url("../img/menu-dot.png") no-repeat left center/contain;
  transition: 0.7s;
}
.main-nav__item:hover a, .main-nav__item:focus a, .main-nav__item:active a, .main-nav__item.active {
  -webkit-text-stroke: 1px #e35d14;
  -webkit-text-fill-color: transparent;
  text-stroke: 1px #e35d14;
  text-fill-color: transparent;
}
.main-nav__item:hover a::before, .main-nav__item:hover a::before, .main-nav__item:hover a::before, .main-nav__item:focus a::before, .main-nav__item:focus a::before, .main-nav__item:focus a::before, .main-nav__item:active a::before, .main-nav__item:active a::before, .main-nav__item:active a::before, .main-nav__item.active::before, .main-nav__item.active::before, .main-nav__item.active::before {
  width: 102px;
}

.back {
  text-stroke: 1px #e35d14;
  text-fill-color: transparent;
  -webkit-text-stroke: 1px #e35d14;
  -webkit-text-fill-color: transparent;
  position: absolute;
  right: 20px;
  top: 90px;
  font-style: italic;
  font-weight: 900;
  font-size: 51px;
  text-transform: uppercase;
  text-decoration: none;
}
.back:hover, .back:focus, .back:active {
  color: #e35d14;
  text-fill-color: #e35d14;
  -webkit-text-fill-color: #e35d14;
  transition: 0.8s;
}

@media (max-width: 785px) {
  .header__phone {
    font-size: 0;
    width: 26px;
    height: 26px;
    background: url("../img/phone.svg") no-repeat center/contain;
  }

  .header__menu {
    font-size: 0;
  }

  .header__burger {
    width: 30px;
    height: 18px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transform: skew(10deg);
  }
  .header__burger span {
    height: 2px;
    width: 100%;
    background: #fff;
    transition: 0.8s ease-out;
  }

  .header__menu.open span::before,
.header__menu.open::before {
    font-size: 0;
  }

  .header__menu.open .header__burger {
    transform: none;
  }
  .header__menu.open .header__burger span {
    background: #e35d14;
  }
  .header__menu.open .header__burger span:nth-of-type(1) {
    transform: rotate(230deg) translateX(-10px);
  }
  .header__menu.open .header__burger span:nth-of-type(2) {
    transform: scale(0);
  }
  .header__menu.open .header__burger span:nth-of-type(3) {
    transform: rotate(-230deg) translateX(-10px);
  }

  .header__right {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
  }

  .header__logo-img {
    width: 115px;
  }

  .header__contact {
    position: absolute;
    bottom: -50px;
    right: 20px;
    font-size: 22px;
  }
  .header__contact::after {
    content: "";
    width: calc(100% + 50px);
    height: 2px;
    position: absolute;
    background: #e35d14;
    bottom: 0;
    left: 0;
  }
}
.menu {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  align-items: flex-start;
  z-index: 12;
}

.menu__link {
  font-size: 12px;
  text-align: right;
  color: #ffffff;
  width: 81px;
  position: relative;
  text-decoration: none;
  opacity: 0.14;
  margin: 10px 0;
  transition: 0.8s;
  border-bottom: 1px solid;
}
.menu__link:hover, .menu__link:active, .menu__link.active {
  opacity: 1;
  width: 90px;
  color: #fff;
}
.menu__link:focus {
  color: #fff;
}
.menu__link.dublicat {
  display: none;
}

.swiper-pagination-bullet {
  font-size: 11px;
  font-weight: 900;
  padding-left: 46px;
  position: relative;
  transition: 0.8s;
}
.swiper-pagination-bullet:not(:last-of-type) {
  padding-bottom: 60px;
  background: url("../img/lines.svg") no-repeat bottom 15px left;
}
.swiper-pagination-bullet::after {
  content: "";
  width: 30px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 7px;
}

.swiper-pagination-bullet-active {
  padding-left: 58px;
}
.swiper-pagination-bullet-active::after {
  width: 100%;
  background-color: #e35d14;
  top: 14px;
}

.home {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.home_bg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.home__content {
  width: 477px;
  margin-top: -10%;
  z-index: 1;
  position: relative;
  left: -200%;
  transition: 2.5s;
}

.swiper-slide-active .home__content {
  left: 0;
  transition: left 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0s;
}

.home_content-title {
  font-style: normal;
  font-weight: bold;
  font-size: 53px;
  text-transform: uppercase;
}

.home_content-title-top {
  color: #000;
  background-color: #fff;
  padding: 4px 9px 0px;
}

.home_content-title-bottom {
  font-size: 43px;
}

.home_content-box {
  display: flex;
  margin-top: 10px !important;
}
.home_content-box li {
  margin-right: 17px;
  font-weight: bold;
  font-size: 15px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
}
.home_content-box li::before {
  content: "";
  width: 5px;
  height: 5px;
  background: #e35d14;
  margin-right: 6px;
  border-radius: 100%;
}

.animation-wrapper {
  position: absolute;
  top: 50%;
  left: 60%;
  width: 400px;
  height: 400px;
  transform: translate(-50%, -50%);
}

.sphere-animation {
  width: 100%;
  height: 100%;
}

.sphere path {
  fill: url(#sphereGradient);
  stroke-width: 1px;
  stroke: rgba(80, 80, 80, 0.35);
  backface-visibility: hidden;
}

@media (min-width: 500px) {
  .sphere path {
    stroke-width: 0.4px;
  }
}
.language {
  position: fixed;
  right: 2%;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 20;
}

.language-icon {
  width: 2px;
  height: 117px;
  background-color: #e35d14;
  display: block;
  order: -1;
}

.header.open .language-items,
.pages__header .language-items {
  color: #000;
}
.header.open .language-items:hover, .header.open .language-items:focus, .header.open .language-items:active, .header.open .language-items.active,
.pages__header .language-items:hover,
.pages__header .language-items:focus,
.pages__header .language-items:active,
.pages__header .language-items.active {
  color: #e35d14;
