.social-round::after {
  content: "";
  width: 99%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: url("../img/social-bg.svg") no-repeat center/contain;
  transform: rotate(280deg);
  z-index: -1;
}

