  background: url("../img/review.png") no-repeat top right/auto 100%;
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(100vw);
}

.review__title {
  margin-left: 0;
  font-weight: 800;
  font-size: 80px;
}

.review__text {
  width: 570px;
  max-width: 100%;
  font-size: 19px;
  color: #7B7B7B;
  margin-bottom: 46px;
  opacity: 0;
  transform: translateY(150vh);
}

.review__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  opacity: 0;
  transform: translateY(50vh);
}

.review__item {
  margin-right: 40px;
}

.review__button {
  margin-left: 15%;
  opacity: 0;
}

.swiper-slide-active .review__box::after {
  transform: translate(0);
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.8s;
}

.swiper-slide-active .review__text {
  opacity: 1;
  transform: translate(0);
  transition: opacity 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.8s, transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.5s;
}

.swiper-slide-active .review__list {
  opacity: 1;
  transform: translate(0);
  transition: opacity 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.8s, transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.5s;
}

.swiper-slide-active .review__button {
  opacity: 1;
  transition: opacity 1s linear 2s;
}

.contacts__title {
  font-weight: 800;
  font-size: 10vh;
  text-transform: uppercase;
  margin-bottom: 100px;
}

.contacts__block {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contacts__bg {
  opacity: 0;
  width: 20%;
  height: 50vh;
  background: url("../img/contacts.png") no-repeat top left/contain;
}

.contacts__item a,
.contacts__item.adres {
  color: #fff;
  font-style: italic;
  font-weight: 900;
  font-size: 5vh;
  display: inline-block;
  margin-bottom: 32px;
  text-decoration: none;
}
.contacts__item a::before,
.contacts__item.adres::before {
  content: "";
  width: 32px;
  height: 32px;
  display: inline-block;
  vertical-align: middle;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 30px;
}

.phone {
  opacity: 0;
}
.phone a::before {
  background-image: url("../img/phone.svg");
}

.adres {
  transform: translateY(-6vh);
  opacity: 0;
}
.adres::before {
  background-image: url("../img/nav.svg");
}

.email {
  transform: translateY(-12vh);
  opacity: 0;
}
.email a::before {
  background-image: url("../img/mail.svg");
}

.social-icon {
  margin-bottom: 20px;
  transform: translateX(100vw);
}

.swiper-slide-active .contacts__bg {
  opacity: 1;
  transition: 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1s;
}

.swiper-slide-active .phone {
  opacity: 1;
  transition: 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.5s;
}

.swiper-slide-active .adres {
  opacity: 1;
  transform: translateY(0);
  transition: 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1s;
}

.swiper-slide-active .email {
  opacity: 1;
  transform: translateY(0);
  transition: 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1.5s;
}

.swiper-slide-active .social-icon {
  transform: translateY(0);
  transition: 1s cubic-bezier(0.1, 0.39, 0, 1.12) 2s;
}

.about-page {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.content-column {
  width: 33%;
}

.about-page__title {
  flex: 1 1 100%;
  font-weight: 900;
  font-size: 66px;
  transform: translateX(150vw);
}
.about-page__title::after {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-style: italic;
  font-weight: 900;
  font-size: 33px;
  color: #fff;
  opacity: 0.2;
  vertical-align: middle;
}

.swiper-slide-active .about-page__title {
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12);
  transform: translateY(0);
}

.accent {
  color: #e35d14;
  font-weight: 900;
  font-style: italic;
}

.underline {
  text-decoration: underline;
  font-weight: 900;
  font-style: italic;
}

.bold {
  font-weight: 900;
  font-style: italic;
}

.screen__first {
  padding-top: 10%;
}

.screen__second .about-page__title {
  margin-bottom: 37px;
}
.screen__second .about-page__title::after {
  content: "Подход";
}

.numbers {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-style: italic;
  font-weight: 900;
  font-size: 19px;
}

.big {
  font-size: 66px;
  line-height: 1.2;
}

.numbers-pad {
  padding-top: 75px;
}

.screen__third .about-page__title {
  margin-bottom: 70px;
}
.screen__third .about-page__title::after {
  content: "Мисия";
}

.grey-text {
  margin-top: 50px;
  opacity: 0.66;
}

.screen__fourth .about-page__title {
  margin-bottom: 35px;
}

.about-page__list li {
  margin-bottom: 5vh;
  position: relative;
  padding-left: 25px;
}
.about-page__list li::before {
  content: "";
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background-color: #e35d14;
  position: absolute;
  top: 7px;
  left: 0;
}

.about-page__footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  color: #fff;
  padding: 20px 0;
}
.about-page__footer .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about-page__footer .contacts__item a {
  font-size: 24px;
  margin-bottom: 0;
  line-height: 1.2;
}
.about-page__footer .contacts__item a::before {
  width: 20px;
  height: 20px;
  margin-right: 15px;
}

.copyright, .confed {
  font-size: 14px;
}

.confed {
  border-bottom: 1px solid;
}

.light {
  font-size: 8px;
  font-weight: 400;
  display: block;
  text-align: right;
}

.about-page__footer .phone,
.about-page__footer .email {
  opacity: 1;
  transform: translateY(-50px);
  transition: transform 0.8s cubic-bezier(0.1, 0.39, 0, 1.12);
}

.about-page__footer .phone a::before {
  background-image: url("../img/call.svg");
}

.about-page__footer .email a::before {
  background-image: url("../img/ios-mail.svg");
}

.pages {
  overflow-x: hidden;
}

.pages__main {
  color: #000;
}

.project__slider {
  display: flex;
  align-items: flex-start;
}

.project__slide-active {
  width: 700px;
}

.project__slider-wrap {
  position: relative;
  max-width: 37%;
  margin-left: auto;
}

.project__slide {
  width: 100% !important;
}

.project__slider .swiper-slide-shadow {
  background: none;
}

.project__slider .swiper-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project__slider-text {
  width: 45%;
  margin-left: auto;
  margin-top: -200px;
  margin-bottom: 75px;
}
.project__slider-text h1 {
  font-weight: 900;
  font-size: 24px;
  margin-bottom: 18px;
}

.project__elements {
  margin-bottom: 75px;
}
.project__elements h2 {
  font-size: 24px;
  text-align: center;
  margin-bottom: 36px;
}

.pages__list {
  max-width: 980px;
  margin: 0 auto !important;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.pages__list li {
  width: 47%;
  padding-left: 25px;
  position: relative;
  margin-bottom: 20px;
}
.pages__list li::after {
  content: "";
  width: 11px;
  height: 11px;
  position: absolute;
  top: 5px;
  left: 0;
  background: #e35d14;
  border-radius: 50%;
}

.projects__pages {
  margin-bottom: 75px;
}
.projects__pages h2 {
  font-size: 24px;
  text-align: center;
  margin-bottom: 36px;
}
.projects__pages .swiper-horizontal > .swiper-scrollbar {
  position: static;
  margin-top: 80px;
}
.projects__pages .swiper-scrollbar-drag {
  background: #e35d14;
}

.project__page-slide p {
  font-weight: bold;
  font-size: 20px;
}

.project__order {
  background: url("../img/order.png") no-repeat right center/auto 100%;
}

.pages__footer {
  padding: 240px 0 40px;
}

.pages__footer-contacts {
  display: flex;
  flex-wrap: wrap;
  max-width: 525px;
  justify-content: space-between;
  align-items: flex-start;
}

.pages__footer-contacts .contacts__item a,
.pages__footer-contacts .contacts__item.adres {
  font-size: 24px;
  margin-bottom: 0;
  line-height: 1.2;
  color: #000;
}
.pages__footer-contacts .contacts__item a::before,
.pages__footer-contacts .contacts__item.adres::before {
  width: 20px;
  height: 20px;
  margin-right: 15px;
}

.pages__footer-contacts .contacts__item {
  margin-bottom: 10px;
}

.pages__footer-contacts .phone,
.pages__footer-contacts .email,
.pages__footer-contacts .adres {
  opacity: 1;
  transform: translateY(-50px);
  transition: transform 0.8s cubic-bezier(0.1, 0.39, 0, 1.12);
}

.pages__footer-contacts .phone a::before {
  background-image: url("../img/call.svg");
}

.pages__footer-contacts .email a::before {
  background-image: url("../img/ios-mail.svg");
}

.pages__footer-contacts .adres::before {
  background-image: url("../img/compass.svg");
}

.pages__footer-text {
  display: flex;
  justify-content: space-between;
}

.why-form--page .form {
  width: 815px;
  max-width: 85%;
}

@media (max-width: 1380px) {
  .pages .language {
    flex-direction: row;
    top: 175px;
    width: 135px;
    transform: none;
    width: 65px;
    justify-content: space-between;
  }

  .pages .language-icon {
    width: 2px;
    height: 28px;
    order: 2;
  }

  .pages .language-items {
    margin-top: 0;
  }

  .pages__header {
    padding-bottom: 70px;
  }

  .project__slider-wrap {
    margin-left: 0;
  }
}
@media (max-width: 1200px) {
  .project__slider-text {
    width: 100%;
    margin-top: 0;
    margin-bottom: 75px;
  }

  .project__slide-active {
    width: 50%;
  }

  .project__slider-wrap {
    max-width: 45%;
  }
}
@media (max-width: 820px) {
  .project__slide-active {
    display: none;
  }

  .project__slider-wrap {
    max-width: 90%;
    overflow: hidden;
  }

  .back {
    font-size: 34px;
  }
}
@media (max-width: 785px) {
  .pages .header__phone {
    background-image: url("../img/call-black.svg");
  }

  .pages .header__burger span {
    background: #000;
  }

  .back {
    font-size: 26px;
    top: 115px;
  }

  .pages .language {
    position: static;
  }

  .project__slider-text {
    margin-bottom: 40px;
  }

  .pages__list li {
    width: 100%;
  }
  .pages__list li::after {
    top: 10px;
  }

  .projects__pages .swiper-horizontal > .swiper-scrollbar {
    margin-top: 41px;
  }

  .project__order {
    background: none;
  }

  .pages__footer-contacts .contacts__item a,
.pages__footer-contacts .contacts__item.adres {
    font-size: 19px;
  }

  .pages__footer-contacts,
.pages__footer-text {
    justify-content: space-evenly;
    text-align: center;
  }

  .pages__footer-text {
    flex-wrap: wrap;
  }
}