.language {
  position: fixed;
  right: 2%;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 20;
}

.language-icon {
  width: 2px;
  height: 117px;
  background-color: $prime-color;
  display: block;
  order: -1;
}

.header.open .language-items,
.pages__header .language-items {
  color: #000;

  &:hover,
  &:focus,
  &:active,
  &.active {
    color: $prime-color;
  }

  &.active {
    order: 2;
    cursor: default;
  }
}

.pages__header .language {
  position: absolute;
  top: 290px;
}

.language-items {
  font-size: 19px;
  text-transform: uppercase;
  margin-top: 15px;
  color: #fff;
  transition: 0.3s;
  order: 1;
  cursor: pointer;

  &:hover,
  &:focus,
  &:active,
  &.active {
    color: $prime-color;
  }

  &.active {
    order: 2;
    cursor: default;
  }
}

.social-round {
  position: fixed;
  bottom: 5%;
  right: 15%;
  display: flex;
  flex-wrap: wrap;
  width: 230px;
  align-items: center;
  justify-content: space-between;
  transition: .8s;
  animation: goSocialAround 10s infinite linear;
  animation-delay: 2s;
  z-index: 111;

  &:hover,
  &:focus {
    animation-play-state: paused;
  }

  &::after {
    content: "";
    width: 99%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: url('../img/social-bg.svg') no-repeat center/contain;
    transform: rotate(280deg);
    z-index: -1;
  }
}

.header.open .social-round::after {
  background-image: url('../img/social-bg-black.svg');
  transform: rotate(10deg);
}

.go.social-round {
  transform: rotate(-360deg);
}

.out.social-round {
  opacity: 0;
  transform: translateX(100vw);
  transition: 0.5s ease-in-out;
  display: none;
}

.in.social-round {
  opacity: 1;
  transform: translateX(0);
  transition: 0.5s ease-in-out;
  display: flex;
}

.social__item {
  transform: rotate(10deg);
  cursor: pointer;
  transition: 0.8s;

  & svg {
    stroke: #fff;
    fill: none;
    transition: 0.8s;
  }

  &:hover svg,
  &:focus svg,
  &:active svg {
    stroke: $prime-color;
    fill: $prime-color;
  }
}

.header.open .social__item:not(.social__item--acc) {

  & svg {
    stroke: #000;
  }

  &:hover svg,
  &:focus svg,
  &:active svg {
    fill: #000;
  }
}

.social__item--acc svg {
  stroke: $prime-color;
}

@keyframes goSocialAround {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.social-round._delete {
    z-index: -1;
    transition: .8s;
}

#smoke {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 0;
}

#lines {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 0;
}

@media (max-width: $mobile-width) {
  .social-round {
    display: none !important;
  }

  .language {
    position: static;
    flex-direction: row;
    transform: none;
    width: 65px;
    justify-content: space-between;
  }

  .language-icon {
    width: 2px;
    height: 28px;
    order: 2;
  }

  .language-items {
    margin-top: 0;
    font-size: 14px;
  }
}
