.page {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  background-color: #000000;
  color: #fff;
}
.page__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.screen {
  flex: 1 0 100%;
  position: relative;
}

.screen__content {
  padding: 150px 25px 25px;
  height: 100%;
  opacity: 0;
  transition: 1.5s;
}

.swiper-slide-active .screen__content {
  opacity: 1;
  transition: 1.5s;
}

.strelca,
.strelca-top {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  stroke: #707070;
  transition: 0.8s;

  & path {
    fill: #707070;
  }
}

.strelca-top {
  top: 20px;
  bottom: auto;
  transform: rotate(180deg);
  z-index: 11;
}

.strelca:hover,
.top:hover,
.strelca-top:hover {
  stroke: $prime-color;

  & path {
    fill: $prime-color;
  }
}

.strelca.swiper-button-disabled,
.top.swiper-button-disabled {
  transform: translateY(150vh);
}

.strelca-top.swiper-button-disabled {
  transform: translateY(-150vh);
}

.top {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateY(150vh);
  cursor: pointer;
  stroke: #707070;
  transition: 0.8s;

  & path {
    fill: #707070;
  }

  &.active {
    transform: translateX(-50%) rotate(180deg);
  }
}
