.pages {
  overflow-x: hidden;
}

.pages__main {
  color: #000;
}

.project__slider {
  display: flex;
  align-items: flex-start;
}

.project__slide-active {
  width: 700px;
}

.project__slider-wrap {
  position: relative;
  max-width: 37%;
  margin-left: auto;
}

.project__slide {
  width: 100% !important;
}

.project__slider .swiper-slide-shadow {
  background: none;
}

.project__slider .swiper-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project__slider-text {
  width: 45%;
  margin-left: auto;
  margin-top: -200px;
  margin-bottom: 75px;

  & h1 {
    font-weight: 900;
    font-size: 24px;
    margin-bottom: 18px;
  }
}

.project__elements {
  margin-bottom: 75px;

  & h2 {
    font-size: 24px;
    text-align: center;
    margin-bottom: 36px;
  }
}

.pages__list {
  max-width: 980px;
  margin: 0 auto !important;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  & li {
    width: 47%;
    padding-left: 25px;
    position: relative;
    margin-bottom: 20px;

    &::after {
      content: "";
      width: 11px;
      height: 11px;
      position: absolute;
      top: 5px;
      left: 0;
      background: $prime-color;
      border-radius: 50%;
    }
  }
}

.projects__pages {
  margin-bottom: 75px;

  & h2 {
    font-size: 24px;
    text-align: center;
    margin-bottom: 36px;
  }

  .swiper-horizontal>.swiper-scrollbar {
    position: static;
    margin-top: 80px;
  }

  .swiper-scrollbar-drag {
    background: rgb(227 93 20);
  }
}

.project__page-slide p {
  font-weight: bold;
  font-size: 20px;
}

.project__order {
  background: url('../img/order.png') no-repeat right center/auto 100%;
}

.pages__footer {
  padding: 240px 0 40px;
}

.pages__footer-contacts {
  display: flex;
  flex-wrap: wrap;
  max-width: 525px;
  justify-content: space-between;
  align-items: flex-start;
}


.pages__footer-contacts .contacts__item a,
.pages__footer-contacts .contacts__item.adres {
  font-size: 24px;
  margin-bottom: 0;
  line-height: 1.2;
  color: #000;

  &::before {
    width: 20px;
    height: 20px;
    margin-right: 15px;
  }
}

.pages__footer-contacts .contacts__item {
  margin-bottom: 10px;
}

.pages__footer-contacts .phone,
.pages__footer-contacts .email,
.pages__footer-contacts .adres {
  opacity: 1;
  transform: translateY(-50px);
  transition: transform 0.8s cubic-bezier(0.1, 0.39, 0, 1.12);
}

.pages__footer-contacts .phone a::before {
  background-image: url('../img/call.svg');
}

.pages__footer-contacts .email a::before {
  background-image: url('../img/ios-mail.svg');
}

.pages__footer-contacts .adres::before {
  background-image: url('../img/compass.svg');
}

.pages__footer-text {
  display: flex;
  justify-content: space-between;
}

.why-form--page .form {
  width: 815px;
  max-width: 85%;
}

@media (max-width: 1380px) {
  .pages .language {
    flex-direction: row;
    top: 175px;
    width: 135px;
    transform: none;
    width: 65px;
    justify-content: space-between;
  }

  .pages .language-icon {
    width: 2px;
    height: 28px;
    order: 2;
  }

  .pages .language-items {
    margin-top: 0;
  }

  .pages__header {
    padding-bottom: 70px;
  }

  .project__slider-wrap {
    margin-left: 0;
  }
}

@media (max-width: 1200px) {
  .project__slider-text {
    width: 100%;
    margin-top: 0;
    margin-bottom: 75px;
  }

  .project__slide-active {
    width: 50%;
  }

  .project__slider-wrap {
    max-width: 45%;
  }
}

@media (max-width: 820px) {
  .project__slide-active {
    display: none;
  }

  .project__slider-wrap  {
    max-width: 90%;
    overflow: hidden;
  }

  .back {
    font-size: 34px;
  }
}

@media (max-width: $mobile-width) {

  .pages .header__phone {
    background-image: url('../img/call-black.svg');
  }

  .pages .header__burger span {
    background: #000;
  }

  .back {
    font-size: 26px;
    top: 115px;
  }

  .pages .language {
    position: static;
  }

  .project__slider-text {
    margin-bottom: 40px;
  }

  .pages__list li {
    width: 100%;

    &::after {
      top: 10px;
    }
  }

  .projects__pages .swiper-horizontal>.swiper-scrollbar {
    margin-top: 41px;
  }

  .project__order {
    background: none;
  }

  .pages__footer-contacts .contacts__item a,
  .pages__footer-contacts .contacts__item.adres {
    font-size: 19px;
  }

  .pages__footer-contacts,
  .pages__footer-text {
    justify-content: space-evenly;
    text-align: center;
  }

  .pages__footer-text {
    flex-wrap: wrap;
  }
}
