.about-page {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.content-column {
  width: 33%;
}

.about-page__title {
  flex: 1 1 100%;
  font-weight: 900;
  font-size: 66px;
  transform: translateX(150vw);

  &::after {
    display: inline-block;
    font-family: $regular-font;
    font-style: italic;
    font-weight: 900;
    font-size: 33px;
    color: #fff;
    opacity: 0.2;
    vertical-align: middle;
  }
}

.swiper-slide-active .about-page__title {
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12);
  transform: translateY(0);
}

.accent {
  color: $prime-color;
  font-weight: 900;
  font-style: italic;
}

.underline {
  text-decoration: underline;
  font-weight: 900;
  font-style: italic;
}

.bold {
  font-weight: 900;
  font-style: italic;
}

.screen__first {
  padding-top: 10%;
}

.screen__second .about-page__title {
  margin-bottom: 37px;

  &::after {
    content: "Подход";
  }
}

.numbers {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-style: italic;
  font-weight: 900;
  font-size: 19px;
}

.big {
  font-size: 66px;
  line-height: 1.2;
}

.numbers-pad {
  padding-top: 75px;
}

.screen__third .about-page__title {
  margin-bottom: 70px;

  &::after {
    content: "Мисия";
  }
}

.grey-text {
  margin-top: 50px;
  opacity: 0.66;
}

.screen__fourth .about-page__title {
  margin-bottom: 35px;
}

.about-page__list li {
  margin-bottom: 5vh;
  position: relative;
  padding-left: 25px;

  &::before {
    content: "";
    width: 11px;
    height: 11px;
    border-radius: 50%;
    background-color: $prime-color;
    position: absolute;
    top: 7px;
    left: 0;
  }
}

.about-page__footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  color: #fff;
  padding: 20px 0;

  & .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  & .contacts__item a {
    font-size: 24px;
    margin-bottom: 0;
    line-height: 1.2;

    &::before {
      width: 20px;
      height: 20px;
      margin-right: 15px;
    }
  }
}

.copyright, .confed {
  font-size: 14px;
}

.confed {
  border-bottom: 1px solid;
}


.light {
  font-size: 8px;
  font-weight: 400;
  display: block;
  text-align: right;
}

.about-page__footer .phone,
.about-page__footer .email {
  opacity: 1;
  transform: translateY(-50px);
  transition: transform 0.8s cubic-bezier(0.1, 0.39, 0, 1.12);
}

.about-page__footer .phone a::before {
    background-image: url('../img/call.svg');
}

.about-page__footer .email a::before {
    background-image: url('../img/ios-mail.svg');
}
