.services__title  {
  text-transform: uppercase;
  margin-left: 0;
  margin-bottom: 50px;
}

.services__list {
  display: flex;
  justify-content: space-between;
  opacity: 0;
  transform: translateY(150vh);
}

.services__item {

  &:nth-of-type(even) .services__item-title {
    color: $prime-color;
  }

  & ul {
    margin-top: -1px;
    padding: 14px 28px;
    border: 1px solid $prime-color;
    list-style: none;
    display: none;

    & li {
      margin-bottom: 25px;
      font-weight: 600;
      font-size: 16px;
    }

    & li:last-of-type {
      margin-bottom: 0;
    }
  }
}

.services__item-title {
  display: inline-block;
  font-weight: 800;
  font-size: 38px;
  text-transform: uppercase;
  border-bottom: 1px solid $prime-color;
  cursor: pointer;
}

.services__block-button {
  text-align: center;
  transform: translate(50px, 150vh);
}

.services__button {
  margin-top: 7%;
  text-transform: capitalize;
  padding: 31px 145px 33px;
}

.swiper-slide-active .services__list {
  opacity: 1;
  transform: translate(0);
  transition: opacity 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1.8s ,transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1.5s;
}

.swiper-slide-active .services__block-button {
  transform: translate(0);
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 2.5s;
}
