.home {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.home_bg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.home__content {
  width: 477px;
  margin-top: -10%;
  z-index: 1;
  position: relative;
  left: -200%;
  transition: 2.5s;
}
.swiper-slide-active .home__content {
  left: 0;
  transition: left 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0s;
}

.home_content-title {
  font-style: normal;
  font-weight: bold;
  font-size: 53px;
  text-transform: uppercase;
}

.home_content-title-top {
  color: #000;
  background-color: #fff;
  padding: 4px 9px 0px;
}

.home_content-title-bottom {
  font-size: 43px;
}

.home_content-box {
  display: flex;
  margin-top: 10px !important;

  & li {
    margin-right: 17px;
    font-weight: bold;
    font-size: 15px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    &::before {
      content: "";
      width: 5px;
      height: 5px;
      background: #e35d14;
      margin-right: 6px;
      border-radius: 100%;
    }
  }
}

.animation-wrapper {
  position: absolute;
  top: 50%;
  left: 60%;
  width: 400px;
  height: 400px;
  transform: translate(-50%, -50%);
}

.sphere-animation {
    width: 100%;
    height: 100%;
}

.sphere path {
  fill: url(#sphereGradient);
  stroke-width: 1px;
  stroke: rgba(80,80,80,.35);
  backface-visibility: hidden;
}

@media (min-width: 500px) {
  .sphere path {
    stroke-width: .4px;
  }
}
