.button {
  font-weight: 700;
  font-size: 27px;
  text-transform: uppercase;
  padding: 31px 52px 33px;
  display: inline-block;
  position: relative;
  margin-top: 30px;
  color: #fff;
  text-decoration: none;
  font-style: italic;
  transition: 0.5s;
  cursor: pointer;

  &::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    border: 1px solid $prime-color;
    transform: skewX(-10deg);
    z-index: -1;
    transition: 0.5s;
  }

  &:hover,
  &:focus,
  &:active {
    color: #fff;
  }

  &:hover::after,
  &:focus::after,
  &:active::after {
    background-color: $prime-color;
  }
}

.button--white {

  &::after {
    border-color: #fff;
  }

  &:hover,
  &:focus,
  &:active {
    color: #000;
  }

  &:hover::after,
  &:focus::after,
  &:active::after {
    background-color: #fff;
  }
}

.button--black {
  color: #fff;

  &::after {
    background-color: #000;
    border-color: transparent;
  }

  &:hover,
  &:focus,
  &:active {
    color: #000;
  }

  &:hover::after,
  &:focus::after,
  &:active::after {
    border-color: #000;
    background-color: transparent;
  }
}
