/*Шрифты*/

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;0,800;0,900;1,400;1,600;1,700;1,900&display=swap');

@font-face {
  font-family: 'Equinox';
  src: url("../fonts/Equinox-Regular.woff") format("woff"),
    url("../fonts/Equinox-Regular.woff2") format("woff2");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

/*Переменные*/
$regular-font: 'Montserrat', sans-serif;
$accent-font: 'Equinox', sans-serif;
$prime-color: #e35d14;
$text-color: #0a0000;
$sec-color: #035c67;
$bg-color: #f7f6f3;
$light-text: #f6f6f6;
$yallow: #ffbf19;
$desktop-width: 1420px;
$mobile-width: 785px;

%wrapper {
  max-width: $desktop-width;
  padding: 0 20px;
  margin: 0 auto;
}

.wrapper {
  @extend %wrapper;
}

.hidden {
  display: none;
  opacity: 0;
  position: absolute;
  transform: translate(-500%);
  width: 0;
  height: 0;
  visibility: hidden;
}

