.menu {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  align-items: flex-start;
  z-index: 12;
}

.menu__link {
  font-size: 12px;
  text-align: right;
  color: #ffffff;
  width: 81px;
  position: relative;
  text-decoration: none;
  opacity: 0.14;
  margin: 10px 0;
  transition: 0.8s;
  border-bottom: 1px solid;

  &:hover,
  &:active,
  &.active {
    opacity: 1;
    width: 90px;
    color: #fff;
  }

  &:focus {
    color: #fff;
  }

  &.dublicat {
    display: none;
  }
}

.swiper-pagination-bullet {
  font-size: 11px;
  font-weight: 900;
  padding-left: 46px;
  position: relative;
  transition: 0.8s;

  &:not(:last-of-type) {
    padding-bottom: 60px;
    background: url('../img/lines.svg') no-repeat bottom 15px left;
  }

  &::after {
    content: '';
    width: 30px;
    height: 2px;
    background-color: #fff;
    position: absolute;
    left: 0;
    top: 7px;
  }
}

.swiper-pagination-bullet-active {
  padding-left: 58px;

  &::after {
    width: 100%;
    background-color: $prime-color;
    top: 14px;
  }
}
