.contacts__title {
  font-weight: 800;
  font-size: 10vh;
  text-transform: uppercase;
  margin-bottom: 100px;
}

.contacts__block {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contacts__bg {
  opacity: 0;
  width: 20%;
  height: 50vh;
  background: url('../img/contacts.png') no-repeat top left/contain;
}

.contacts__item a,
.contacts__item.adres {
  color: #fff;
  font-style: italic;
  font-weight: 900;
  font-size: 5vh;
  display: inline-block;
  margin-bottom: 32px;
  text-decoration: none;

  &::before {
    content: "";
    width: 32px;
    height: 32px;
    display: inline-block;
    vertical-align: middle;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    margin-right: 30px;
  }
}

.phone {
  opacity: 0;

  a::before {
    background-image: url('../img/phone.svg');
  }
}

.adres {
  transform: translateY(-6vh);
  opacity: 0;

  &::before {
    background-image: url('../img/nav.svg');
  }
}

.email {
  transform: translateY(-12vh);
  opacity: 0;

  a::before {
    background-image: url('../img/mail.svg');
  }
}

.social-icon {
  margin-bottom: 20px;
  transform: translateX(100vw);
}

.swiper-slide-active .contacts__bg {
  opacity: 1;
  transition: 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1s;
}

.swiper-slide-active .phone {
  opacity: 1;
  transition: 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.5s;
}

.swiper-slide-active .adres {
  opacity: 1;
  transform: translateY(0);
  transition: 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1s;
}

.swiper-slide-active .email {
  opacity: 1;
  transform: translateY(0);
  transition: 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1.5s;
}

.swiper-slide-active .social-icon {
  transform: translateY(0);
  transition: 1s cubic-bezier(0.1, 0.39, 0, 1.12) 2s;
}
