.portfolio__title {
  text-transform: uppercase;
  font-size: 10vh;
  opacity: 0;
}

.portfolio__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  transform: translate(50px, 150vh);
}

.portfolio__item {
  width: 33%;
  position: relative;
  padding-bottom: 15px;
  text-align: center;

  &:hover .portfolio__link,
  &:focus .portfolio__link {
    border-color: $prime-color;
    transform: scale(1) skew(-10deg);
  }
}

.portfolio__link {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  margin-top: -50%;
  margin-left: -50%;
  display: inline-block;
  border: 1px solid transparent;
  z-index: 5;
  transform: scale(0.1);
  transition: 0.8s;
}

.portfolio__item-title {
  font-weight: 900;
  font-size: 24px;
}

.portfolio__catigoria {
  font-weight: 900;
  font-size: 17px;
  display: inline-block;
  padding: 9px 15px 10px;
  position: absolute;
  top: 6px;
  right: 36px;
  z-index: 2;

  &::before {
    content: "";
    width: 100%;
    height: 100%;
    background-color: $prime-color;
    transform: skew(-10deg);
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
  }
}

.portfolio__button {
  text-align: center;
  transform: translate(50px, 150vh);

  & .button {
    margin-top: 0;
  }
}

.portfolio__title {
  transition: opacity 1s ease-out 1s;
  opacity: 1;
}

.swiper-slide-active .portfolio__list {
  transform: translate(0);
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1s;
}

.swiper-slide-active .portfolio__button {
  transform: translate(0);
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 1.5s;
}
