#order.fix {
  position: fixed;
  bottom: 0;
  left: 0;
}

.form {
  padding: 60px 150px 20px 0;
  width: 59%;
  position: relative;

  &::after {
    content: "";
    height: 100%;
    width: 200%;
    background: url('../img/orange-bg.svg') no-repeat right center/cover;
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
    transform: translateX(-150vw);
  }
}

.form__items {
  display: inline-block;
  vertical-align: bottom;
  width: 49%;
  min-width: 200px;
  opacity: 0;
  transition: 1s;
}

.form__item-on {
  display: flex;
  flex-direction: column-reverse;
  align-items: flex-start;
}

.form__label {
  display: inline-block;
  font-weight: 600;
  color: #fff;
  font-size: 19px;
  display: block;
  transition: 0.3s;
}

.form__label-on {
  font-weight: 900;
  transform: translate(10px, 20px);
}

.form__input {
  background: transparent;
  color: #fff;
  border: none;
  border-bottom: 2px solid #EC905E;
  padding: 6px 10px;
  box-shadow: none;
  margin-bottom: 35px;

  &:focus,
  &:focus-visible {
    border-color: #fff;
    outline: none;
  }

  &:focus + .form__label-on,
  &:focus-visible + .form__label-on {
    transform: translate(0);
  }
}

.form__textarea {
  margin-bottom: 28px;
}

.form__button {
  position: absolute;
  z-index: 2;
  bottom: -43px;
  right: 50px;
  font-size: 35px;
  font-weight: 900;
  padding: 24px 30px 22px;
  transform: translateX(150vw);
}

.swiper-slide-active .form__items,
.project__order .form__items {
  opacity: 1;
}

.swiper-slide-active .form__button,
.project__order .form__button {
  transition: transform 1.5s ease-in-out;
  transform: translateX(0);
}

.swiper-slide-active .form::after,
.project__order .form::after {
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0.5s;
  transform: translateX(0);
}

.modal {
  display: none;
}

.modal__overlay {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(#000, 0.8);
}

.modal-form {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 650px;
  max-width: 100%;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: #000;
    border: 1px solid $prime-color;
    transform: skew(-10deg);
  }
}

.modal-form__title {
  font-size: 27px;
  margin: 35px 0;
}

.modal-form__input {
  width: 80%;
  padding: 21px 32px;
  background: #fff;
  color: #000;
  margin-bottom: 25px;
  border: none;
  box-shadow: none;
}

.modal-form__button {
  width: 100%;
  text-align: center;
  margin-top: 0;
  transform: translateX(-24px);

  &::after {
    background: $prime-color;
  }

  &:hover,
  &:focus {
    color: #fff;

    &::after {
      background: transparent;
    }
  }
}
